.App {
  text-align: left;
}

.App h1 {
   text-align: center;
 }

.instruction {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 5px;
}

.App-link {
  color: #61dafb;
}

.ConfirmationModal {
    border-radius: 5px;
    border: 2px solid #ccc;
}

.separator {
  height: 7px;
  background-color: grey;
  border: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
